.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.footer .white-text {
  height: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 30px;
}

@media (max-width: 767.98px) {
  .footer {
    font-size: 14px;
    line-height: 1.4;
  }
}